/*
 *   Next or Previous Article
 *    Styles for the next or previous Article
 *
 *  Version:
 *    1.0.0 - 2022/03/22
 */

@import '../base.scss';

.c-article-next-previous {

  @include t-gutters;
  @include t-v-gutters;

  &__text-content {
    margin-top: 1.25rem;
  }

  &__pretitle {
    color: $s-color--font-1;
    margin-bottom: 1.5625rem;
  }

  &__title {
    margin-bottom: 2.25rem;
    color: $s-color--font-1;
  }

  &__excerpt {
    margin-bottom: 1rem;
    color: $s-color--font-5;
  }

  &__btn {
    color: $s-color--font-1;
    font-weight: $s-font--weight-bold;
  }

  // *** Media Queries *** //
  @include t-media-query($s-small-up) {
    &__inner {
      display: flex;
      align-items: center;
      cursor: pointer;
    }

    &__image-wrapper {
      width: 45.5%;
      flex-shrink: 0;
      order: 2;

      .is-reversed & {
        order: 0;
      }
    }

    &__text-content {
      margin: 1.5% 15% 0 0;
      text-align: left;
      width: 33%;

      .is-reversed & {
        margin: 1.5% 3.7% 0 11%;
      }
    }

    &__pretitle {
      display: block;
      margin-bottom: 2%;
    }

    &__title {
      line-height: 1.17;
      margin-bottom: 4.7%;
    }

  }

  @include t-media-query($s-medium-up) {
    &__title {
      font-size: t-to-rem(52);
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__title {
      font-size: t-to-rem(72);
    }
  }
}